#app2{

}

#app2 .tab-bar{
    display: flex;
}

#app2 .tab-bar > li{
    width: 50%;
    font-size: 16px;
    line-height: 26px;
    padding:2px 0px;
    border:1px solid grey;
    text-align: center;
}

#app2 .tab-bar > li.selected{
    background:rgb(253, 221, 155);
    color:white;
}

#app2 .tab-content{

}

#app2 .tab-content > li{
    display: none;
}

#app2 .tab-content > li.active{
    display: block;
}